<template>
<main-layout :backgroundImage="`${$cloudinary}main.png`" backgroundOffset="50">

  <template #header>
    <personal-info-header :userinfo="`${myPosition ? myPosition : '--'} / ${rating.at(-1).index} місце у рейтингу`"/>
  </template>

  <template #body>
    <div class="section-name">Рейтинг Учасників</div>
    <br/>
    <div class="rating-section">
      <div v-for="(user, i) in rating" :key="i">
        <div class="rating-item" :class='{ "rating-item-me": user.index == myPosition }'>
          <span class="user-index">{{ user.index }}.</span>
          <span class="user-icon material-icons-outlined" :style="{ color: userColors[i] }">account_circle</span>
          <div class="user-info">
            <div class="user-name">
              <i v-if="i<3" class="material-icons-outlined" :style='{ color: medalColors[i] }'>workspace_premium</i>&nbsp;
              <span>{{ user._id }}</span>
            </div>
            <div>
              <i class="material-icons-outlined" :style="{ color: userProgress[i]>0 ? $green : userProgress[i]==0 ? '#3498DB' : $red }">
                {{ userProgress[i]>0 ? "arrow_upward" : userProgress[i]==0 ? "remove" : "arrow_downward" }}
              </i>&thinsp;
              <b>{{ user.score }} балів</b>
            </div>
          </div>
        </div>
      </div>
    </div>
  </template>

</main-layout>
</template>

<script>
import mainLayout from '@/layouts/main-layout.vue'
import personalInfoHeader from '@/components/personal-info-header.vue'
export default {
  components: { mainLayout, personalInfoHeader },
    data() { return {
    username: localStorage.getItem('username'),
    medalColors: ['#c9b037', '#b4b4b4', '#ad8a56']
  }},
  computed: {
    rating() { return this.$store.state.rating },
    userScores() { return this.rating.map(e => e.score) },
    userColors() { return this.userScores.map(e => this.mix3(e / this.userScores[2] * 100)) },
    userProgress() { return this.rating.map((e, i) => Math.sign( (e?.indexPrev || Infinity) - e.index)) },
    myPosition() { return this.rating.find(e => e._id == this.username)?.index },
  }
}
</script>

<style lang="scss" scoped>
//* {border: 1px solid red}
.section-name {
  font-size: 1.15em;
  font-weight: bold;
  padding: 1em 1.5em;
  margin-bottom: .5em;
  border-radius: 4px;
  text-transform: uppercase;
  background: rgba(0,0,0,.025);
}
.rating-section {
  margin: auto;
  border-radius: 4px;
  box-shadow: 0 2px 16px 5px rgba(0,0,0,.05);
}
.rating-item {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  column-gap: 15px;
  padding: 1.35em max(3vmax, 20px);
  background: #fff;
  border-bottom: 1px solid #0001;
  .user-index { 
    width: 2ch;
    text-align: center;
    font-size: 1.25em;
    font-weight: bold;
    font-family: 'Oswald';
  }
  .user-info {
    flex-grow: 1;
    display: flex;
    line-height: 1.5em;
    @media (max-width: 480px) { flex-direction: column }
  }
  .user-name { flex-grow: 1; }
  .user-icon { font-size: 2em }
  .user-score { font-weight: 700 }
}
.rating-item-me {
  font-weight: bold;
}
.material-icons-outlined { 
  font-size: 1.5em;
  vertical-align: middle
}
</style>